import "core-js/modules/es.array.push.js";
import fetchWithAuth from '@/utils/fetchWithAuth';
export default {
  data() {
    return {
      // 一级设置：上传渠道
      channels: [{
        value: 'telegram',
        label: 'Telegram'
      }, {
        value: 'cfr2',
        label: 'CloudFlareR2'
      }, {
        value: 's3',
        label: 'S3'
      }],
      activeChannel: 'telegram',
      // 当前选中的上传渠道

      // 二级设置：Telegram 配置
      telegramSettings: {
        loadBalance: {},
        channels: []
      },
      tgRules: {
        name: [{
          required: true,
          message: '请输入渠道名',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            const names = this.telegramSettings.channels.map(item => item.name);
            if (names.filter(name => name === value).length > 1) {
              callback(new Error('渠道名不能重复'));
            } else if (value === 'Telegram_env') {
              // 判断该渠道保存位置是否为环境变量
              const savePath = this.telegramSettings.channels.find(item => item.name === value).savePath;
              if (savePath !== 'environment variable') {
                callback(new Error('渠道名不能为保留值'));
              } else {
                callback();
              }
            } else {
              callback();
            }
          },
          trigger: 'blur'
        }],
        botToken: [{
          required: true,
          message: '请输入 Bot Token',
          trigger: 'blur'
        }],
        chatId: [{
          required: true,
          message: '请输入 Chat ID',
          trigger: 'blur'
        }]
      },
      // 二级设置：CFR2 配置
      cfr2Settings: {
        channels: []
      },
      // 二级设置：S3 配置
      s3Settings: {
        loadBalance: {},
        channels: []
      },
      s3Rules: {
        name: [{
          required: true,
          message: '请输入渠道名',
          trigger: 'blur'
        }, {
          validator: (rule, value, callback) => {
            const names = this.s3Settings.channels.map(item => item.name);
            if (names.filter(name => name === value).length > 1) {
              callback(new Error('渠道名不能重复'));
            } else if (value === 'S3_env') {
              // 判断该渠道保存位置是否为环境变量
              const savePath = this.s3Settings.channels.find(item => item.name === value).savePath;
              if (savePath !== 'environment variable') {
                callback(new Error('渠道名不能为保留值'));
              } else {
                callback();
              }
            } else {
              callback();
            }
          },
          trigger: 'blur'
        }],
        endpoint: [{
          required: true,
          message: '请输入 Endpoint',
          trigger: 'blur'
        }],
        bucketName: [{
          required: true,
          message: '请输入存储桶名称',
          trigger: 'blur'
        }],
        region: [{
          required: true,
          message: '请输入存储桶区域',
          trigger: 'blur'
        }],
        accessKeyId: [{
          required: true,
          message: '请输入访问密钥 ID',
          trigger: 'blur'
        }],
        secretAccessKey: [{
          required: true,
          message: '请输入机密访问密钥',
          trigger: 'blur'
        }]
      },
      // 加载状态
      loading: false
    };
  },
  computed: {
    // 当前选中渠道的标签
    activeChannelLabel() {
      const channel = this.channels.find(item => item.value === this.activeChannel);
      return channel ? channel.label : '';
    }
  },
  methods: {
    addChannel() {
      switch (this.activeChannel) {
        case 'telegram':
          this.telegramSettings.channels.push({
            id: this.telegramSettings.channels.length + 1,
            name: '',
            type: 'telegram',
            savePath: 'database',
            botToken: '',
            chatId: '',
            enabled: true,
            fixed: false
          });
          break;
        case 'cfr2':
          // this.cfr2Settings.channels.push({
          //     id: this.cfr2Settings.channels.length + 1,
          //     name: '',
          //     type: 'cfr2',
          //     savePath: 'database',
          //     enabled: true,
          //     fixed: false
          // });
          this.$message.error('R2渠道请通过绑定 R2 存储桶或通过 S3 渠道添加');
          break;
        case 's3':
          this.s3Settings.channels.push({
            id: this.s3Settings.channels.length + 1,
            name: '',
            type: 's3',
            savePath: 'database',
            accessKeyId: '',
            secretAccessKey: '',
            region: '',
            bucketName: '',
            endpoint: '',
            pathStyle: false,
            enabled: true,
            fixed: false
          });
          break;
      }
    },
    deleteChannel(index) {
      switch (this.activeChannel) {
        case 'telegram':
          // 调整 id
          this.telegramSettings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.telegramSettings.channels.splice(index, 1);
          break;
        case 'cfr2':
          // 调整 id
          this.cfr2Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.cfr2Settings.channels.splice(index, 1);
          break;
        case 's3':
          // 调整 id
          this.s3Settings.channels.forEach((item, i) => {
            if (i > index) {
              item.id -= 1;
            }
          });
          this.s3Settings.channels.splice(index, 1);
          break;
      }
    },
    saveSettings() {
      // 所有表单的 Promise 数组
      let validationPromises = [];

      // Telegram
      if (this.$refs.tgChannelForm) {
        this.$refs.tgChannelForm.forEach(form => {
          validationPromises.push(new Promise(resolve => {
            form.validate(valid => resolve(valid));
          }));
        });
      }

      // S3
      if (this.$refs.s3ChannelForm) {
        this.$refs.s3ChannelForm.forEach(form => {
          validationPromises.push(new Promise(resolve => {
            form.validate(valid => resolve(valid));
          }));
        });
      }

      // 等待所有验证完成
      Promise.all(validationPromises).then(results => {
        const isValid = results.every(valid => valid);
        if (!isValid) {
          return;
        }

        // 保存设置
        const settings = {
          telegram: this.telegramSettings,
          cfr2: this.cfr2Settings,
          s3: this.s3Settings
        };
        fetchWithAuth('/api/manage/sysConfig/upload', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(settings)
        }).then(() => {
          this.$message.success('设置已保存');
        });
      });
    }
  },
  mounted() {
    this.loading = true;
    // 获取上传设置
    fetchWithAuth('/api/manage/sysConfig/upload').then(response => response.json()).then(data => {
      this.telegramSettings = data.telegram;
      this.cfr2Settings = data.cfr2;
      this.s3Settings = data.s3;
    }).finally(() => {
      this.loading = false;
    });
  }
};