import fetchWithAuth from '@/utils/fetchWithAuth';
export default {
  data() {
    return {
      settings: {
        telemetry: {},
        randomImageAPI: {},
        cloudflareApiToken: {}
      },
      // 加载状态
      loading: false
    };
  },
  computed: {},
  methods: {
    saveSettings() {
      fetchWithAuth('/api/manage/sysConfig/others', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(this.settings)
      }).then(() => this.$message.success('设置已保存'));
    }
  },
  mounted() {
    this.loading = true;
    // 获取上传设置
    fetchWithAuth('/api/manage/sysConfig/others').then(response => response.json()).then(data => {
      this.settings = data;
    }).finally(() => {
      this.loading = false;
    });
  }
};